import * as React from "react";
import './Menu.css'
import AnimeServices from "../../sevices/anime.services";
import {GridColDef} from "@mui/x-data-grid";
import VerticalTabs, {VerticalTab} from "../../ui/tab-panel/vertical-tabs/vertical-tabs.component";
import FormatShapesSharpIcon from '@mui/icons-material/FormatShapesSharp';
import sessionService from "../../sevices/session.service";
import EqualizerIcon from '@mui/icons-material/Equalizer';
import {generateColumns, rowCustomisation} from "../../helpers/toolls";
import {IAnime, ObjectId, TabComponent, TabProps} from "../../types/types";
import TableComponent from "../../ui/table/table.component";
import {Tab, TabClasses} from "@mui/material";
import TabPanelComponent from "../../ui/tab-panel/tab-panel.component";

export interface MenuStates {
    rows: IAnime[];
    fields: GridColDef[];
    result: TabResult;
}

interface TabResult{
    components: TabComponent[],
    props: TabProps[];
}

export default class MenuBodyComponent extends React.Component<any, MenuStates> {

    id: ObjectId = {
        "$oid": ''
    }
    animeA: IAnime = {
        _id: this.id,
        image: '',
        name: '',
        full_name: '',
        quality: 0,
        rating: 0,
        release_date: '',
        time: 0,
        subscribe: false,
        episodes: 0,
        actions: ''
    }
    animeU: IAnime = {
        _id: this.id,
        image: '',
        full_name: '',
        quality: 0,
        episodes: 0,
        release_date: '',
        rating: 0,
        time: 0,
        subscribe: false
    }

    constructor(props: any) {
        super(props);
        this.getAnime = this.getAnime.bind(this)
        this.state = {rows: [], fields: [], result: {components: [], props: []}}
        this.generateTabs = this.generateTabs.bind(this)
        this.a11yProps = this.a11yProps.bind(this)
    }

    componentDidMount() {
        this.getAnime()
    }

    getAnime(): IAnime[] {
        AnimeServices.getAll().subscribe((response) => {
                if (response.status === 200) {
                    this.setState({rows: response.data.data})
                } else {
                    alert(response.statusText)
                }

            }
        )
        return this.state.rows
    }


    generateTabs() {
        let tabs: TabComponent[] = []
        let props: TabProps[] = []
        if (sessionService.isAdmin()) {
            tabs.push(
                {
                    component: <TableComponent rows={rowCustomisation(this.state.rows)}
                                               columns={(row: IAnime[]) => generateColumns(this.animeU, this.state.rows)}/>,
                },
                {
                    component: <TableComponent rows={rowCustomisation(this.state.rows)}
                                               columns={(row: IAnime[]) => generateColumns(this.animeA, this.state.rows)}/>,
                }
            )
            props.push(
                {
                label: 'Anime List',
                icon: <FormatShapesSharpIcon/>
            },
                {
                    label: 'Manage Anime',
                    icon: <EqualizerIcon/>
                })
        } else {
            tabs.push(
                {
                    component: <TableComponent rows={rowCustomisation(this.state.rows)}
                                               columns={(row: IAnime[]) => generateColumns(this.animeU, this.state.rows)}/>,
                }
            )
            props.push(
                {
                    label: 'Anime List',
                    icon: <FormatShapesSharpIcon/>
                })
        }
        this.setState({result: {
                components: tabs,
                props: props
            }})
    }

    a11yProps(index: number) {
        return {
            id: `vertical-tab-${index}`,
            'aria-controls': `vertical-tabpanel-${index}`,
        };
    }

    render() {
        return (
            <div>
                <VerticalTabs other={this.state.result.props} tabComponent={this.state.result.components}
                              tabs={this.generateTabs()} key={1}/>
            </div>
        );
    }
}